*{
  margin : 0;
  padding:0;
  box-sizing: border-box;
}
.container{
  margin: 0px 175px;
  padding: 20px 0px;
  text-align: center;
}

.section-container{
  margin-top:10px;
  background-color: white;
  padding: 10px;
  border-radius: 20px;
  width: 80vw;
  /* margin-left: 110px; */
}

textarea{
  height:350px;
  width:100%;
  border: 2px solid #ced4ce;
  border-radius: 15px;
}

.btn-convert{
  background-color: #5095f5;
  border: 1px solid #5e555e;
  padding:14px;
  color: #fff;
  width:325px;
  border-radius: 10px;
  font-size: 14px;
  margin-top:15px;
  text-align : center;
}

.audio-container{
  position: fixed;
  width:100%;
  bottom :0;
  background-color: #ffffff;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.audio-button{
  font-size:45px;
  border: transparent;
  background: transparent;
}

.audio-button:disabled{
  opacity: .5;
}

.icon-btn{
  margin-top:30px;
  color: #5095f5;
  
}

.progress-container{
  width:100%;
  background-color: #ccc;
  height: 6px;
  margin: 0 0 10px;
}

.progress-bar{
  background-color: #007bff;
  height:100%;
  width:0;
}